
.auth__main{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(https://www.diariomotor.com/imagenes/2020/06/ayudas-coche-nuevo-seat-leon-rojo-2020-1536x899.jpg);
    margin: 0px;
    height: 100vh;
    width: 100vw;
}

.auth__maintitle{
    
    justify-content: center;
    margin-bottom: 80px;
    margin-top: 50px;
    font-size: 80px;
    height: 100px;
    width: 400px;
    background-color: white;
}

.auth__box-contanier{
    background-color: white;
    box-shadow: 0px 3px $dark-grey;
    border-radius: 2px;
    padding: 20px;
    width: 250px;
}

.auth__title{
    color:darken($color: $primary, $amount: 20)
}

.auth__input{
    color: $dark-grey;
    border: 0px;
    border-bottom: 1px solid $light-grey;
    font-size: 16px;
    margin-top: 10px;
    margin-bottom: 10px;
    height: 20px; 
    width: 100%;

    transition: border-bottom .3s ease;

    &:focus{
        border-bottom: 1px solid $primary;
        outline: none;
    }
}

.auth__social-networks{
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
}

.auth__alert-error{
    background-color: red;
    border-radius: 5px;
    color: white;
    display: flex;
    justify-content: center;
    margin-top: 10px;
    padding: 5px;

}