
.btn{
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
    font-size: 12px;
    padding: 7px 10px 7px 10px;
    transition: color .3s ease;

    &:focus{
        outline: none;
    }

    &:hover{
        color: darken($color: white, $amount: 20);
    }
}

.btn-primary{
    background-color: $primary;
    border-radius: 2px;

    &:disabled {
        background-color: lighten($color: $primary, $amount: 15);
    }

    &:hover{
        background-color: darken($color: $primary, $amount: 15);
    }
}

.btn-kavak{
    background-color: white;
    color: black;
    height: 45px;
    border-radius: 10px;
    font-size: 15px;
    transition: color .3s ease;
    width: 270px;
    margin-top: 5px;
    margin-bottom: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;

    &:hover{
        background-color: darken($color: black, $amount: 15);
        color: white;
    }
}

.btn-search{
    background-color: blue;
    color: white;
    height: 45px;
    border-radius: 10px;
    font-size: 15px;
    transition: color .3s ease;
    width: 270px;
    margin-top: 5px;
    margin-bottom: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;

    &:hover{
        background-color: darken($color: blue, $amount: 15);
        color: white;
    }
}


.btn-danger{
    background-color: red;
    transition: background-color .3s ease;

    &:hover{
        background-color: darken($color: red, $amount: 15);
    }
}

.btn-block{
    width: 100%;
}

.google-btn {
    cursor: pointer;
    margin-top: 5px;
    width: 100%;
    height: 42px;
    background-color: $google-blue;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);

    transition: box-shadow .3s ease;

    .google-icon-wrapper {
        position: absolute;
        margin-top: 1px;
        margin-left: 1px;
        width: 40px;
        height: 40px;
        border-radius: 2px;
        background-color: $white;
    }
    .google-icon {
        position: absolute;
        margin-top: 11px;
        margin-left: 11px;
        width: 18px;
        height: 18px;
    }
    .btn-text {
        float: right;
        margin: 11px 40px 0 0;
        color: $white;
        font-size: 14px;
        letter-spacing: 0.2px;
    }
    &:hover {
        box-shadow: 0 0 6px $google-blue;
    }
    &:active {
        background: $button-active-blue;
    }
}

